@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
*,
html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page-container {
  background-color: hsl(243, 100%, 89%);
  border-radius: 0px 0px 20px 20px;
  border-top: 1.5px solid #fff;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: 60vh
}

.container {
  background-color: hsl(243, 100%, 89%);
  border-radius: 20px 20px 0px 0px;
  max-width: 400px;
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
}

header > h1 {
  font-size: 45px;
  font-family: Lobster;
  font-weight: 100;
  color: hsl(240, 100%, 99%);
  text-shadow: 0 0 5px hsl(0, 0%, 57%) ;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

form > input {
  margin: 15px
}

form > input[type=text] {
  width: 240px;
  height: 30px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  text-align: center;
  background-color: white;
  background-repeat: no-repeat;
  transition: width 0.3s ease-in-out;
}

form > input[type=text]:focus {
  width: 245px;
}

.container-btn{
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
  width: 240px
}

.container-btn > button{
  background-color: hsl(243, 100%, 89%);
  border: 1.5px solid #fff;
  border-bottom: none;
  border-radius: 5px 5px 0px 0px;
  font-size: 14px;
  width: 33%;
  padding-top: 5px;
  padding-left: 1px;
  padding-right: 1px;
  padding-bottom: 5px;
  margin-left: 2px;
  margin-right: 2px;
}

.container-btn > button:active{
  background-color: hsl(0, 0%, 100%);
}

.container-btn > button:hover {
  box-shadow: 0 3px 4px 2px gray;
}



.container-perfil {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-perfil > h2 {
  color: rgb(255, 255, 255);
  font-weight: 400;
  margin-bottom: 15px;
}

.container-perfil > img {
  width: 150px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.container-perfil > span {
  text-align: center;
  font-weight: 100;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 10px;
}

.container-perfil > a {
  text-decoration: none;
}
.container-repos {
  overflow-y: scroll;
  font-size: 13px;
  height: auto;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.container-repos::-webkit-scrollbar {
  display: none;
}

.container-repos {
-ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.container-repos > h2 {
  color: rgb(255, 255, 255);
  font-weight: 400;
  margin-bottom: 15px;
}

ul > li {
  font-size: 17px;
  font-weight: 300;
  margin: 6px;
  padding: 8px;
  background-color: rgba(211, 213, 236, 0.815);
  text-align: center;
}
h2 {
  text-align: center;
  font-weight: 300;
}

.search-octocat {
  width: 300px;
  opacity: 0.2;
  border-radius: 0px 00px 15px 15px;
}
.container-not {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-not > h2 {
  color: rgb(255, 255, 255);
  font-weight: 400;
  margin-bottom: 15px;
  padding: 15px;
}

 img {
  width: 350px;
  border-radius: 10%;
}

.container-not > a {
  margin-top: 18px;
  font-size: 20px;
  text-decoration: none;
}
